/* Book.css */

.btn{

}
.r{
    transform: rotate(180deg);
    position: absolute;
    right:0;

}
.s{
    transform: scale(.7);
    
    position: absolute;
    right:50%;
}
.flipbook-container {
    width: 400px;
    height: 600px;
    background-color: #f2f2f2;
    border: 1px solid #ddd;
  }
  .s-page{
display: grid;


grid-template-rows: .4fr .6fr;
  }
  .txt-container{
font-size: 2rem;
width: 80%;
  }
  .img-container{
display: flex;
margin-top: 40px;
justify-content: center;
align-items: center;
  }
  .img
  {
    width:80%;
    height: 90%;
    transform: rotate(4deg);
    margin-left: -60px;
  }
  .img:before,
.img:after {
  content: "j";
  position: absolute;
  width: 60px;
  height: 60px;
  background-color: #ffcc00;
}
  .page {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    padding: 20px;
height: 80vh;  
}
.demoPage{
    height:30vh;
}
  
  .page:nth-child(odd) {
    background-color: #ff6868;
  }
  .demoPage{
    background-color: '#E8DCB8';
  }
  
  .page:nth-child(even) {
    background-color: #f2f2f2;
  }
  