/* Landing.css */

.landing-container {
    margin-top:-100px;
    display: flex;
    padding-left: 100px;

}
  
  .image-container {
    width: 50%;
    /* padding-top: 100px; */
    text-align: center;
  }
  
  .text-container {
    width: 50%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: left;
  }
  
  .text-container h1 {
    text-align: left;
    font-size: calc(16px + 3vw);
  }
  
  .text-container p {
    text-align: left;
    font-size: calc(12px + 1vw);
  }
  
  .svg-container {
    width: 80%;
    padding-top: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .svg-container svg {
    max-width: 100%;
    height: auto;
  }
  
  
  @media (max-width: 900px) {
    .landing-container {
      flex-direction: column;
    }
  
    .image-container,
    .text-container {
      width: 100%;
      text-align: center;
    }
    .text-container p {
text-align: center;
    }
    .text-container h1 {
        text-align: center;

    }
.text-container{
align-items: center;
}
.landing-container{
    flex-direction: column-reverse;
    padding-left:0px;
margin-top: 0;
}
.svg-container{
    margin-inline: auto;
}
  }
  